body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
    position: fixed;
    top: 0%;
    left: 0;
    right: 0;
    z-index: 2;
}
nav li {
    display: inline-block;
    padding: 26px 0;
    width: 33.3%;
    text-align: center;
    color: #932691;
}
nav {
    background-color: #6fda9d;
}
.jumbotron {
   position: relative;
   background: white;
   background-size: cover;
   height: 400px;
   margin-top: 65px;
}
.jumbotron .overlay {
   position: absolute;
   top: 0;
   right: 0;
   bottom: 0;
   left: 0;
   z-index: auto;
   background: -webkit-linear-gradient(#333, black);
   background: linear-gradient(#333, black);
   opacity: 0.7;
}
.jumbotron h1 {
   position: absolute;
   top: 20%;
   width: 100%;
   color: #932691;
}
.jumbotron h3 {
   position: absolute;
   bottom: 20%;
   width: 100%;
   color: #932691;
}
.jumbotron .top-left {
   position: absolute;
   top: 0%;
   left: 2%;
}
.jumbotron .top-right {
   position: absolute;
   top: 0%;
   right: 2%;
}
.jumbotron .bottom-left {
   position: absolute;
   bottom: 0%;
   left: 2%;
}
.jumbotron .bottom-right {
   position: absolute;
   bottom: 0%;
   right: 2%;
}
.jumbotron img {
height: 100px;
width: 150px;
background: transparent;
-webkit-animation: App-logo-spin infinite 5s linear;
        animation: App-logo-spin infinite 5s linear;
}

@-webkit-keyframes App-logo-spin {
   from {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg);
   }
   to {
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg);
   }
 }

@keyframes App-logo-spin {
   from {
     -webkit-transform: rotate(0deg);
             transform: rotate(0deg);
   }
   to {
     -webkit-transform: rotate(360deg);
             transform: rotate(360deg);
   }
 } 
.carousel {
    background: #631821;
}

.carousel .carousel-item {
    height: 200px;
    width: 200px; 
    border: 1px solid black;
}
img {
    height: inherit;
    width: inherit;
    background: #6fda9d;
}
.carousel .carousel-item:hover {
    box-shadow: 0 0 40px #FFD64F;
}
.page-footer .footer-copyright {
margin-bottom: 20px;
}
.page-footer {
    background: #6fda9d;
    padding-bottom: 20px;
}
